<template>
	<div class="page">
		<header-vue fixed 
					class="music-header-2" 
					:title="topinfo.ListName + `ç¬¬${_getDayOfYear(data.update_time)}å¤©`" 
					:showTitle="showTitle">
	      <fallback slot="left"></fallback>
	      <span slot="right" style="font-size: 30px;font-weight: bold;display: inline-block;margin-top: -10px;">...</span>
	    </header-vue>
	    
	    <div class="page-content" style="overflow: hidden;">
	    	<div class="music-cover-wrap">
		    	 <div class="music-cover" 
		    	 	  ref="musicCover" 
		    	 	  :style="{backgroundImage:`url(${topinfo.pic_album})`}"></div>
		    	 <div class="title-wrap">
		    	 	<p class="main-title" v-if="topinfo.ListName">{{ topinfo.ListName }} ç¬¬{{ data.update_time | getDayOfYear }}å¤©</p>
		    	 	<p class="minor-title" v-if="data.update_time">{{ data.update_time }}æ´æ°</p>
		    	 </div>
		    	 <div class="cover-overlay"></div>
		    </div>
	    	<div class="song-cotainer" v-if="songlist.length" ref="scrollTarget">
    			<mt-navbar :value="selected" @input="function(val) {selected = val}">
				  <mt-tab-item id="1">åæ²</mt-tab-item>
				  <mt-tab-item id="2">è¯¦æ</mt-tab-item>
				  <!-- <mt-tab-item id="3">æ­è¯æ¬</mt-tab-item> -->
				</mt-navbar>
				<mt-tab-container v-model="selected" ref="scrollTouch">
					<mt-tab-container-item id="1">
						<ul>
							<li>
								<mt-cell class="music-cell-type4">
									<a><img src="../assets/play.png" class="icon">éæºæ­æ¾å¨é¨</a>
									<!-- <div>
										<a><img src="../assets/download.png" class="icon">ä¸è½½</a>
										<a style="margin-left: 10px;"><img src="../assets/choice.png" class="icon">å¤é</a>
									</div> -->
								</mt-cell>
							</li>
							<li v-for="(song, index) in songlist" 
								key="index"
							    @click="playMusic(songlist, index, song.data.songid)">
								<mt-cell class="music-cell-type3">
									<div class="suffix">
										<p :style="index<3 && {color: '#FF4500'}">{{ index + 1}}</p>
										<p>
											<span class="icon" 
												  :style="{backgroundImage: `url(${require('../assets/value-up.png')})`}"></span>
											{{ song.in_count | convertListenCount }}
										</p>
									</div>
									<div class="song">
										<p>{{ song.data.songname }}</p>
										<p>{{ song.data.singer | spliceSinger }} Â· {{ song.data.albumname }}</p>
									</div>
									<span class="detail">Â·Â·Â·</span>
								</mt-cell>
							</li>
						</ul>
					</mt-tab-container-item>
					<mt-tab-container-item id="2" v-if="selected == 2">
						<p  v-html="topinfo.info" 
							style="color: rgba(0, 0, 0, .5); 
							padding: 0 15px;
							font-size: 14px;
							line-height: 22px;
							margin-top: 20px;"></p>
					</mt-tab-container-item>
				</mt-tab-container>
	    	</div>
	    	<div class="ranklist-loading" v-else>
	    		<div class="loading">
	    			<mt-spinner type="fading-circle"></mt-spinner>
	    			<p>æ­£å¨è½½å¥...</p>
	    		</div>
	    	</div>
    	</div>
	</div>
</template>

<script>
	import fallback from './fallback.vue';
	import { apiHandler } from '@/api/index';
	import AlloyTouch from 'alloytouch';
	import Transform from 'css3transform';
	import { lyricsAnalysis, getDayOfYear } from '../util';
	import base64 from 'base-64';
	import utf8 from 'utf8';
	import { jsonp } from '@/api/index';

	/* ==============================================================
	 *                       RankList ç»ä»¶
	 *	å·²å®æUIï¼
	 *		éçåè¡¨æ»å¨å®æå°é¢çæ¨¡ç³åååæ é¢çåå
	 *  Issuse:
	 *		å¨è¿éçè¯åå¤§å®¶æ¨èä¸AlloyTeamç AlloyTouch
	 *		èä¸æ¯Iscrollï¼ç¸æ¯è¾äºIscrollï¼AlloyTouchæ´å ç®æ´
	 *		300å¤è¡çä»£ç ç¸æ¯IScroll2000å¤çè¡çä»£ç ï¼å¤§å¤§åå°äºèµæºçå ç¨,
	 *		æºç çéè¯»ä¹éå¸¸æµç.ç¸æ¯ä¹ä¸AlloyTouchä¹èµäºäºæ»å¨æ´å¤ççµæ´»æ§
	 *		èä¸åIscrollå¿é¡»è¦æ±WapperåScrollçéå¶ï¼åæ¶Iscrollå¯¹äºé«åº¦
	 *		çä¸¥æ ¼è¦æ±å¨Vueçæ¨¡æ¿ä¸­ç»å¸¸ä¼åºç°æ æ³è·åæç¡®é«åº¦èå¯¼è´æ æ³æ»å¨ï¼éè¦
	 *		éè¿ Refresh æ¥å·æ°ç»ä»¶, èAlloyTouchåä¸ä¼åè¿ç§æåµ
	 * ============================================================= */

	export default {
		name: 'rankList',
		created() {
	        apiHandler({
	        	name: 'rankList',
	        	params: {
	        		topid: this.$route.params.id
	        	}
	        },(response) => {
	        	/*
	        	 * å»¶è¿400msæ§è¡ç­å¾é¡µé¢åæ¢å¨ç»å®æ
	        	 * åç±: å½ä¸å­å¨å»¶è¿æ¶ç»ä»¶çåå®¹æ¸²æä¸é¡µé¢çåæ¢å°ä¼åæ¶æ§è¡
	        	 * ç±æ­¤å°ä¼å¯¼è´å¨Chromeä¸äº§çå¡é¡¿
	        	 * */
		        setTimeout(() => {
		        	this.topinfo = response.topinfo;
			        this.songlist = response.songlist;
			        this.data = response;

			        // enable scroll 
			        this.$nextTick(() => {
			        	this._initScroll();
			        });
		        }, 400);
	        })
		},
		data() {
			return {
				topinfo: {},
				title: '',
				showTitle: false,
				data: {},
				songlist: [],
				selected: "1",
			};
		},
		methods: {
			_initScroll() {
				let scrollTouch = this.$refs.scrollTouch.$el,
					scrollTarget = this.$refs.scrollTarget;

				Transform(scrollTarget, true);
				let self = this;	
				let alloyTouch = new AlloyTouch({
					touch: scrollTouch,
					target: scrollTarget,
					sensitivity: .8,
					bindSelf: true,
					property: 'translateY',
					max: 0,
					change(pos) {
						let coverHeight = -scrollTouch.clientWidth*.7 + 40;
						function enableScroll() {
							this.fixed = scrollTouch.scrollTop > 0 ? true : false;
						};

						// Toggle The Title When Pos Change
						if (pos <= -60) {
							self.showTitle = true;
						}else {
							self.showTitle = false;
						}
						if (pos <= coverHeight) {
							// Fiexd The RankList When List Scroll To Top
							this.target[this.property] = coverHeight;

							// enable tab container scrolled When List Scroll To Top
							this.preventDefault = false;
							enableScroll.call(this)
						}else {
							this.preventDefault = true;
						}
						self._blurringCover(pos/coverHeight);
					}
				});
			},
			_blurringCover(percentage) {
				let blur = 30,
					musicCover = this.$refs.musicCover;
				musicCover.style.filter = `blur(${(percentage*blur >> 0)}px)`;
			},
			_getDayOfYear: getDayOfYear
		},
		components: {
			headerVue(resolve) {
				require(['./header.vue'], resolve);
			}
		}
	}
</script>

<style lang="sass">
	.music-cover-wrap {
		position: relative;
		margin-top: -$header-height;
		z-index: 0;
		.music-cover {
			 width: 100%;
			 background-repeat: no-repeat;
			 background-size: cover;
			 transition: blur .3s ease;
			 &:after {
			 	display: block;
			 	content: '';
			 	padding-top: 90%;
			 }
		}
		.title-wrap {
			position: absolute;
			width: 100%;
			bottom: 25%;
			color: #ffffff;
			z-index: 3;
			.main-title, .minor-title {
				text-align: center;
				margin-top: 0;
				margin-bottom: 5px;
			}
			.main-title {
				font-size: 18px;
			}
			.minor-title {
				font-size: 14px;
			}
		}
		.cover-overlay {
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			background-color: rgba(0, 0, 0, .7);
			z-index: 2;
		}
	}
	.song-cotainer, .ranklist-loading {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		background-color: transparent;
		overflow: hidden;
		&:before {
			display: block;
			content: '';
			margin-top: 70%;
		}
		.lyrics-wrapper {
			line-height: 42px;
		    text-align: center;
		    font-size: 16px;
		}
	}
	.ranklist-loading {
		bottom: 0;
		.loading {
			display: flex;
			justify-content: center;
			padding-top: 30%;
			height: 100%;
			background-color: $white-base;
			p {
				margin-top: 10px;
				margin-left: 10px;
				font-size: 10px;
				color: rgba(0, 0, 0, .5);
			}
		}
	}
</style>


